@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Light.woff2") format("woff2"), url("../fonts/OpenSans-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }

body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.5;
  min-width: 320px;
  margin: 0; }

button {
  display: block;
  background-color: #0AD9C6;
  border: 0; }

.body-lock {
  overflow-y: scroll;
  position: fixed; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1440px;
  margin: 0 auto; }

.header__bg {
  width: 100%;
  height: auto; }

.header__adv {
  position: absolute;
  width: 350px;
  top: 51.4%;
  left: 50%;
  transform: translate(-126%);
  color: #ffffff; }
  @media (max-width: 1189px) {
    .header__adv {
      top: 36.4%; } }

.adv__title {
  font-size: 4.6rem;
  font-weight: 600;
  line-height: 1.4;
  font-family: "Open Sans", "Arial", sans-serif;
  margin: 24px 0 24px 0; }

.adv__content {
  font-size: 2rem;
  font-weight: 300;
  line-height: 1.6;
  font-family: "Open Sans", "Arial", sans-serif;
  color: white; }

.nav {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
  width: 873px;
  min-height: 62px;
  padding: 0 23px 0 17px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2); }

.nav__list {
  display: flex;
  justify-content: space-between;
  padding: 0;
  margin: 0;
  list-style: none;
  margin-top: 26px; }

.nav__link {
  display: block;
  text-decoration: none;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.7); }

.nav__burger {
  display: none;
  position: absolute;
  top: 7px;
  right: 0;
  width: 30px;
  height: 3px;
  background-color: #000000;
  transition: 0.3s; }
  .nav__burger::before, .nav__burger::after {
    content: "";
    position: absolute;
    top: -7px;
    right: 0;
    width: 30px;
    height: 3px;
    background-color: #000000;
    transition: 0.3s; }
  .nav__burger::after {
    top: 7px; }

.nav--menu-active .nav__burger {
  width: 0; }

.nav--menu-active .nav__burger::before {
  transform: translateY(7px) rotate(-45deg); }

.nav--menu-active .nav__burger::after {
  transform: translateY(-7px) rotate(45deg); }

.nav--menu-active .nav__list {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 30px;
  left: -30px;
  width: 210px;
  padding: 0 30px 20px 30px;
  align-items: flex-start; }
  @media (min-width: 1024px) {
    .nav--menu-active .nav__list {
      position: static;
      padding: 0;
      flex-direction: row;
      align-items: center;
      width: unset; } }

.nav--menu-active .nav__item {
  margin-top: 20px; }
  @media (min-width: 1024px) {
    .nav--menu-active .nav__item {
      margin-top: 0; } }

