		width: 100%;
		margin: 30px auto 0 auto;
	}
}*/
.nav__toggle {
  display: none; }

.no-js .nav__list {
  position: static;
  display: flex;
  padding: 0;
  flex-direction: row;
  align-items: center;
  width: 100%; }

.form {
  position: absolute;
  top: 49.9%;
  right: 50%;
  transform: translate(134%);
  width: 328px; }
  @media (max-width: 1189px) {
    .form {
      top: 34.9%; } }

.form__title {
  color: #ffffff;
  font-size: 17px;
  font-weight: 600;
  letter-spacing: -0.025em;
  text-align: center;
  margin-bottom: 22px; }

.form input,
.form button[type="submit"] {
  border: none;
  width: 100%;
  border-radius: 4px; }

.form input {
  height: 48px;
  margin-bottom: 20px;
  padding: 0 30px; }
  .form input::placeholder {
    font-size: 15px;
    color: rgba(28, 28, 28, 0.5);
    letter-spacing: 0.03em; }

.form button[type="submit"] {
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.4;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  letter-spacing: -0.02em;
  height: 51px; }
